import * as React from "react"
import Flashcards from "../../components/Flashcards"
import Quiz from "../../components/Quiz"

export default function IntroToAI() {
  return (
    <div style={{ color: "#00ff90", background: "#000", padding: "2rem" }}>
      <h1>Introduction to AI/ML in AWS</h1>
      <p>
        Artificial Intelligence (AI) and Machine Learning (ML) in AWS allow you to build, train, and deploy models at scale.
        AWS offers prebuilt services (like Rekognition, Comprehend) and frameworks for custom training (like SageMaker).
      </p>

      <h2>Flashcards</h2>
      <Flashcards topic="intro-to-ai" />

      <h2>Quiz</h2>
      <Quiz topic="intro-to-ai" />
    </div>
  )
}
